import gsap from 'gsap';
import ScrollTrigger from 'gsap/ScrollTrigger';
import React, { useLayoutEffect, useRef } from 'react'
import styled, { ThemeProvider } from 'styled-components';
import Accordion from '../Accordion';
import { light } from '../../styles/Themes';


const Section = styled.section`
min-height: 100vh;
height: auto;
width: 100vw;
background-color: ${props => props.theme.text};
position: relative;
color: ${(props) => props.theme.body};
overflow: hidden;


display: flex;
justify-content: center;
align-items: center;
flex-direction: column;
`
const Title = styled.h1`
  font-size: ${(props) => props.theme.fontxxl};
  text-transform: uppercase;
  color: ${(props) => props.theme.body};
  
  margin: 1rem auto;
  border-bottom: 2px solid ${(props) => props.theme.carouselColor};
  width: fit-content;

  @media (max-width: 48em){
  font-size: ${(props) => props.theme.fontxl};

  }
`;

const Container = styled.div`
width: 75%;
margin: 2rem auto;

display: flex;
justify-content: space-between;
align-content: center;

@media (max-width: 64em){
  width: 80%;
  }
  @media (max-width: 48em){
  width: 90%;
  flex-direction: column;

  &>*:last-child{
    &>*:first-child{

    margin-top: 0;
}

  }
  }
`
const Box = styled.div`
width: 45%;
@media (max-width: 64em){
  width: 90%;
  align-self: center;
  }

`

const Faq = () => {

const ref = useRef(null);
gsap.registerPlugin(ScrollTrigger);
useLayoutEffect(() => {
  
  let element = ref.current;

  ScrollTrigger.create({
    trigger: element,
    start:'bottom bottom',
    end:'bottom top',
    pin:true,   
    pinSpacing:false, 
    scrub:1,
    // markers:true,
  })

  return () => {
    ScrollTrigger.kill();
  };
}, [])

  return (
    <ThemeProvider theme={light}>
    <Section ref={ref} id="faq">
    <Title>Faq</Title>

    <Container>

<Box>
  <Accordion ScrollTrigger={ScrollTrigger} title="What is the course meeting time?" >
  Tuesday/Thursday: 9:30am - 10:45am 12 Waverly, Room L120 <br /><br />
  In-person attendence is expected, but if you cant attend in person for some reason on a particular day, let me know in advance and I will create a Zoom for the class at https://nyu.zoom.us/j/5771576027
  <br /><br />
  The above Zoom link can also be found in Brightspace under the Zoom tab. It would be helpful if you turn your camera on if you can.
  <br />

  I will make an effort to record the sessions, but live attendence is preferred if possible. Links to recordings of the Zoom meetings will also be found on Brightspace after they are made.
  </Accordion>
  <Accordion ScrollTrigger={ScrollTrigger} title="What are the prequisites for this course?" >
  Three years of high school mathematics or equivalent. Students who have taken or are taking CSCI-UA 101 will not receive credit for this course. Note: This course does not count towards the major in computer science, although it is a prerequisite for students with no previous programming experience for CSCI-UA 101, Introduction to Computer Science (one can major in CS after taking 101). It is offered every semester in multiple sessions.

  </Accordion>
  <Accordion ScrollTrigger={ScrollTrigger} title="What are the course office hours?" >
  I will alternate between office hours in-person and on Zoom. My office hour this semester is on Fridays, from 6:45pm - 7:45pm. My office is in Room 328 in Warren Weaver Hall (the Courant Institute building) if you want to meet me in person. <br /><br />
  Please turn your camera on if possible when you connect.<br /><br />

If you are unavailable at these times and would like to talk to me, please email me for an appointment.
 
  </Accordion>
  <Accordion ScrollTrigger={ScrollTrigger} title="What is the grading breakdown for this course?" >
  Your grade in this course will be determined based on the breakdown below. Note that the following rubric serves as a guideline for how grades will be calculated. The final percentage allotted to each category is at my discretion.
  <br /><br />
  <ul>
    <li>Assignments: 30%</li>
    <li>Quizzes: 5%</li>
    <li>In-class participation: 5%</li>
    <li>Midterm Exam: 30%</li>
    <li>Final Exam: 30%</li>
  </ul>
  </Accordion>
  <Accordion ScrollTrigger={ScrollTrigger} title="How do assignment submissions work?" >
  The submission time recorded on Brightspace is the only time that I can use for determining if an assignment was completed on time. Timestamps on your local computer that indicate that a file was not modified after a specific date are not something that I can take into account when it comes to late submissions. It is very easy to manipulate these timestamps.<br /><br />

Please test your assignments thoroughly before submitting them to Brightspace.<br /><br />

If you realize you want to change or update your assignment, just submit it to Brightspace again, as long as it is before the deadline. The graders will use the most recent version submitted.<br /><br />

It is your responsibility to make sure that your assignments have been submitted successfully. You can do this by simply attempting to download your work after it has been uploaded to the system  if you are able to do so, your assignment was submitted successfully.<br /><br />

Do not send your code files via email attachments. NYUs email servers will reject files with code files as attachments. If you are having trouble with Brightspace, you can send me code via a private message on Ed (see below for more on Ed).<br /><br />

All grading will be done on Brightspace and you can check your grades at any time by logging in and clicking on the Grades tool.
  </Accordion>
  <Accordion ScrollTrigger={ScrollTrigger} title="What support is there for the course material?" >
  Computer science is similar to math, in that the material is cumulative in nature in that concepts build on each other. This means its important that you not fall behind. If at any time you have questions, feel like youre falling behind, or dont understand the material there is always help available. The following resources are here to help you in the event you struggle with any concepts:<br /><br />
<ul>
<li>CS Tutors - Please refer to the common course syllabus for more information on the types of tutoring that are offered. Before visiting a tutor please review our tutor and student guidelines for what is expected (tutors are not there to do your work for you.)</li><br /><br />

<li>CAS Tutors - the <a href="https://www.nyu.edu/students/academic-services/undergraduate-advisement/academic-resource-center/tutoring-and-learning.html" style={{color: 'lightblue'}}>University Learning Center</a> provides free tutoring.</li><br /><br />

<li>Office Hours</li>
</ul>
  </Accordion>
  <Accordion ScrollTrigger={ScrollTrigger} title="What are the accomodations if I have a disability?" >
  Academic accommodations are available for students with disabilities. Please contact the Moses Center for Students with Disabilities (212-998-4980 or <a href="mailto:mosescsd@nyu.edu" style={{color: 'lightblue'}}>mosescsd@nyu.edu</a>) for further information. Students who are requesting academic accommodations are advised to reach out to the Moses Center as early as possible in the semester for assistance, although it can be done at any time during the semester.
  </Accordion>
  <Accordion ScrollTrigger={ScrollTrigger} title="What is the grading scale for the course?" >
  A: 93 - 100 <br></br>
  A-: 90 - 93 <br></br>
  B+: 87 - 90 <br></br>
  B: 84 - 87 <br></br>
  B-: 80 - 84 <br></br>
  C+: 77 - 80 <br></br>
  C: 74 - 77 <br></br>
  C-: 70 - 74 <br></br>
  D: 65 - 70 <br></br>
  F: 0 - 65 <br></br>
  </Accordion>
</Box>
<Box>
<Accordion ScrollTrigger={ScrollTrigger} title="Am I in the right course?" >
If you have some limited experience with computer programming, you may want to take CSCI-UA.0003 (Introduction to Computer Programming, Limited Prior Experience) instead of this course. <br /><br />

If you wish to place out of this course into CSCI-UA.0101 (Introduction to Computer Science) and you have experience with computer programming using C, C++, Java, JavaScript, Python or Matlab you should take the NYU Computer Science Placement Exam for Freshmen, even if you are a sophomore or junior. See the separate web page on the NYU Computer Science Placement Exam for Freshmen ( Sample Placement Exams ) You may use either C, C++, Java, JavaScript, Python or Matlab to answer the questions. <br /><br />

Students should email the CS undergraduate program administrator at undergrad@cs.nyu.edu to set up a test. Tests can be completed asynchronously and online, and should only take an hour to complete.<br /><br />
  </Accordion>
  <Accordion ScrollTrigger={ScrollTrigger} title="Is there a textbook for this course?" >
  You dont need to read or purchase a textbook for this class (sufficient materials will be available to you through the online modules and the lectures), but if you would like to do so, the following two texts are recommended.<br /><br />

The first one is freely available online (you can also buy a printed copy). The second costs money, but you can save by buying a earlier (3rd or 4th) edition, which will work fine and would usually be cheaper.<br /><br />

Think Python, Second Edition - Jeffrey Elkner, Allen B. Downey, and Chris Meyers<br /><br />

Starting Out with Python - 5th Edition - Tony Gaddis<br /><br />
  </Accordion>
  <Accordion ScrollTrigger={ScrollTrigger} title="What is the course format?" >
  This course uses a blended learning format. This means is that a good portion of the lecture and reading material are presented in interactive online modules, which you will be expected to complete before the same topics are covered in class.<br /><br />

The online modules contain video, text and interactive programming exercises. When you are finished wtih a module, you will be asked to take a short online quiz (via Brightspace) to test your understanding of the concepts presented in the module.<br /><br />

One class period each week (the lecture, but I try to make it as interactve as possible) will focus on the application of the principles taught in the online learning module.<br /><br />

Weekly assignments will be presented in the other class period, the workshop. I  will be available in class to provide support and guidance in starting work on an assignment.<br /><br />

  </Accordion>
  <Accordion ScrollTrigger={ScrollTrigger} title="What are the course quizzes?" >
  There will be 11 online quizzes that go along with each online learning module. These quizzes contain a series of multiple choice questions and will be worth 10% of your final grade. Quizzes are delivered via Brightspace and can be attempted up to 5 times. Your most recent score will be the one recorded (not your highest score). The online quizzes should be completed before class on the day in which they are due. Please see the course schedule for a complete list of due dates. Grace days (described in the Assignments FAQ) do not apply to quizzes.
  </Accordion>
  <Accordion ScrollTrigger={ScrollTrigger} title="What are the course assignments?" >
  There will be 11 assignments in this course that will be assigned on a weekly basis. For a full outline of assignment due dates please refer to the course schedule page.<br /><br />

I suggest that you use cloud storage (e.g. Google Drive, Dropbox, etc.) to save your work on your assignments. That way, if your machine crashes, you wont lose your work. Also, remember to save your work frequently. Speaking as someone who has sometimes lost work, I say Save early, save often.<br /><br />

It is important not to get behind in turning your assignments (this class is cumulative). Late submissions will be penalized by 10% off per day late, and assignments that are more than 8 days late can not be turned in for credit.<br /><br />

That being said, sometimes there are unforeseen situations that may impede your progress, as a result you are being given eight grace days for the entire semester. These grace days cover all late submissions regardless of personal, professional, or technical related delays (e.g. a job interview, computer hardware failure, etc.) and no further exceptions will be granted (so dont waste them simply by procrastinating). You dont need to do anything special to use these grace days - the course graders will keep track of these on your behalf and will apply them to your work as necessary.<br /><br />

You will be using Brightspace to turn in your homework assignments. It is your responsibility to make sure that your assignments have been submitted successfully. You can do this by simply attempting to download your work after it has been uploaded to the system  if you are able to do so, your assignment was submitted successfully.<br /><br />

For example, lets say you need a little extra time with Assignment #1 and you miss the deadline by one day. After you submit your program to Brightspace, and you would lose one grace day, leaving you with 7 for the rest of the semester. You can choose to mix and match how you use your grace days, and you do not need to let me know that you are using one (we will keep track of this information). If you run out of grace days and you turn in an assignment late you will be penalized by 10% per day late.<br /><br />

There may be some extra credit components to some of the assignments, but these will only count toward your assignment grades, and wont help you if you get a perfect score on the required parts of the assignments, only if you dont.<br /><br />


  </Accordion>
  <Accordion ScrollTrigger={ScrollTrigger} title="Can I partner with someone on the assignments?" >
  Assignments that you turn in should be your own work. It is fine to talk to other students and to get assistance in how to do something, but you should not ask your fellow students to actually do the work for you. When you turn in an assignment, you are saying that you have done this work yourself. The definition of plagiarism is to present someone elses work as though it were your own. Please read the Computer Science Department <a href="https://cs.nyu.edu/home/undergrad/policy.html" style={{color: 'lightblue'}}>statement on academic integrity</a> for more information.<br /><br />

We have software to detect copied programs. If student A turns in student Bs program as their own, both students will receive a zero, and there may be other adverse consequences.<br /><br />

With that said, you may work in pairs on assignments. If you choose to work with a partner both you and your partner must turn in your own copy of the assignment via Brightspace. If only one team member submits a copy of an assignment and the second team member does not submit anything the second team member will receive a grade of zero. If you are working as a team you should credit your partner as a comment at the top of your code. For example:<br /><br />
  <code>
  """<br />
  Assignment #1<br />
  Vladimir Havlik (worked with Katherine Chan)<br />
  """
  </code>
  </Accordion>
  <Accordion ScrollTrigger={ScrollTrigger} title="What NYU Computing Facilities are there?" >
  NYU provides <a href="https://www.nyu.edu/life/information-technology/teaching-and-learning-services/studios-and-computing-labs/student-technology-centers-computer-labs.html" style={{color: 'lightblue'}}>computer labs</a> around campus.

Note about saving your work in the labs: You will be able to save your work done in the ITS labs under your NYU Home Account and/or on your own flash drives. Although you can write to the hard disks of the machines in the labs, you cannot be sure that you will have access to the same machine the next time you enter the lab and the drives in the lab are frequently erased. The best option is to upload your files online and download them as needed.
  </Accordion>
</Box>
    </Container>
    </Section>
    </ThemeProvider>
  )
}

export default Faq
